<template>
  <div class="changepassword" v-if="loginUser">
    <div class="zhuce">
      <span>当前手机号：</span><span>{{ loginUser.phone }}</span>
    </div>
    <van-form @submit="onSubmit">
      <van-field v-model="passwordb" type="password" name="password" placeholder="设置新密码" />
      <van-field
        v-model="passworda"
        type="password"
        name="password"
        placeholder="确认新密码"
      />
      <van-field
        v-model="sms"
        name="sms"
        placeholder="请输入验证码"
        :rules="[{ required: true, message: '请填写验证码' }]"
      >
        <template #button>
          <van-button
            round
            size="small"
            :disabled="countdown < 60"
            @click.stop="getSms('register')"
            native-type="button"
            type="warning"
            >{{ countdown >= 60 ? "发送验证码" : `${countdown}秒` }}</van-button
          >
        </template>
      </van-field>
      <div class="buttonsub" style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >提交</van-button
        >
      </div>
    </van-form>
  </div>
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
export default {
  data() {
    return {
      loginUser: null,
      passworda: "",
      passwordb: "",
      sms: "",
      countdown: 60,
      smskey: "",
    };
  },
  created() {
    this.$store.getters.loginUser;
    this.loginUser = this.$store.getters.loginUser.data;
    console.log(this.$store.getters.loginUser);
    this.axios.get("/api/verify_code").then((d) => {
      console.log(d);
      this.smskey = d.data.data.key;
    });
  },
  methods: {
    getSms() {
      this.axios
        .post("api/register/verify", {
          //post数据
          key: this.smskey, //获取短信的key
          phone: this.loginUser.phone, //手机号
          code: "",
        })
        .then((d) => {
          if (d.data.status == 200) {
            //如果发送成功,发送按钮禁用,同时倒计时60秒
            let intervalid = setInterval(() => {
              if (this.countdown <= 0) {
                clearInterval(intervalid);
                this.countdown = 61;
              }
              this.countdown--;
            }, 1000);
            this.$toast("验证码发送成功");
          } else {
            this.$toast(d.data.msg);
          }
        });
    },
    onSubmit(values) {
      console.log("submit", values);
      this.axios
        .post("/api/register/reset", {
          //post数据
          account: this.loginUser.phone, //手机号
          captcha: values.sms,
          code: "",
          password: values.password,
        })
        .then((d) => {
          if (d.data.status == 200) {
            this.$toast("修改成功");
            this.loginOut();
            this.$router.push({ name: "Login"});
          } else {
            this.$toast(d.data.msg);
          }
        });
    },
    ...mapMutations(["saveToken", "saveLoginInfo", "loginOut"]),
  },
  computed: {
    ...mapGetters(["loginUser"]),
  },
};
</script>
<style lang="less" scoped>
.changepassword {
  min-height: 100vh;
  background-color: white;
  .zhuce {
    margin: 27.5px 0 26.5px 0;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .buttonsub {
    margin: 16px 0;
    .van-button--info {
      color: #fff;
      background: -webkit-gradient(
        linear,
        left top,
        right top,
        from(#f35447),
        to(#ff8e3c)
      );
      background: linear-gradient(90deg, #f35447 0, #ff8e3c);
      background: -moz-linear-gradient(to right, #f35447 0, #ff8e3c 100%);
      border: none;
    }
  }
}
</style>